.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: #f4f4f9;
  font-family: 'Arial', sans-serif;
}

h1 {
  color: #333;
  font-size: 2.5rem;
  margin-bottom: 20px;
  text-align: center;
}

label {
  font-size: 1.2rem;
  margin-bottom: 10px;
  display: block;
  color: #555;
}

.select-container {
  margin-bottom: 20px; /* Space between dropdown questions */
  display: flex;       /* Added flexbox for label and select to be inline */
  align-items: center; /* Align vertically */
  justify-content: center; /* Align horizontally */
  width: 100%;
}

label span {
  margin-right: 25px;  /* Added more space between the question text and dropdown */
}

select {
  padding: 8px 12px;
  font-size: 1rem;
  margin-top: 10px;
  margin-bottom: 20px;
  border: 1px solid #ccc;
  border-radius: 4px;
  background-color: #fff;
  color: #333;
}

button {
  background-color: #6200ea;
  color: #fff;
  padding: 10px 20px;
  font-size: 1rem;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 10px;
}

botton:hover {
  background-color: #3700b3;
}

h2 {
  font-size: 1.8rem;
  margin-top: 20px;
  color: #008000
}

button.reset {
  background-color: #ff1744;
  margin-top: 20px;
}

button.reset:hover {
  background-color: #d50000;
}

@media (max-width: 768px) {
  .App {
    padding: 10px;
    text-align: center;
  }

  .select-container {
    flex-direction: column;
    align-items: flex-start;
  }

  label span {
    margin-right: 0;
    margin-bottom: 5px;
  }

  select {
    width: 100%;
  }

  button {
    width: 100%;
    max-width: 300px;
  }

  button.reset {
    width: 100%;
    max-width: 300px;
  }
}